/* Profile Page */
.profile-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  background: #f4f4f4;
  min-height: 100vh;
}

h2 {
  font-size: 26px;
  color: #333;
  margin-bottom: 20px;
}

/* Profile Card */
.profile-card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  width: 350px;
  text-align: center;
}

.profile-avatar {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  margin-bottom: 15px;
  border: 3px solid #ff6600;
}

.profile-info p {
  font-size: 16px;
  color: #555;
  margin: 8px 0;
}

/* Edit Button */
.edit-btn {
  background: #ff6600;
  color: white;
  padding: 10px 15px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  margin-top: 15px;
  width: 100%;
}

.edit-btn:hover {
  background: #e65c00;
}

.edit-icon {
  font-size: 18px;
}

/* Modal Overlay */
.edit-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Modal Content */
.modal-content {
  background: white;
  padding: 25px;
  border-radius: 12px;
  width: 90%;
  max-width: 400px;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.2);
  text-align: center;
}

.modal-content h3 {
  margin-bottom: 20px;
  color: #333;
}

.modal-content input {
  width: 100%;
  padding: 10px 12px;
  margin-bottom: 12px;
  border: 1px solid #ccc;
  border-radius: 6px;
  font-size: 15px;
}

/* Modal Buttons */
.modal-actions {
  display: flex;
  justify-content: space-between;
  gap: 10px;
}

.save-btn,
.cancel-btn {
  padding: 10px 14px;
  border: none;
  border-radius: 6px;
  font-size: 15px;
  cursor: pointer;
  width: 50%;
}

.save-btn {
  background: #28a745;
  color: white;
}

.cancel-btn {
  background: #ccc;
  color: #333;
}

.save-btn:hover {
  background: #218838;
}

.cancel-btn:hover {
  background: #bbb;
}
