.home-container {
  padding: 20px;
  text-align: center;
  background-color: #f9f9f9;
}

/* Ad Slider */
.slider-container {
  max-width: 80%;
  margin: auto;
}

.ad-image {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 8px;
}

/* Product Categories */
.category-title {
  margin-top: 30px;
  font-size: 24px;
  font-weight: bold;
  color: #333;
}

.categories-grid {
  display: flex;
  gap: 15px;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 20px;
}

.category-card {
  width: 150px;
  text-align: center;
  padding: 10px;
  border-radius: 8px;
  background: white;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.2s ease-in-out;
}

.category-card:hover {
  transform: scale(1.05);
}

.category-image {
  width: 100%;
  height: 120px;
  object-fit: contain; /* Prevent cropping */
  border-radius: 8px;
  background-color: #f0f0f0; /* Helps when image has transparency or empty space */
  margin-bottom: 10px;
}
